import React from 'react';
import './Technologies.scss';
import html from '../../images/html.png'
import css from '../../images/css.png'
import js from '../../images/js.png'
import react from '../../images/react.png'
import redux from '../../images/redux.png'
import ts from '../../images/ts.png'
import en from '../../images/Resume.EN.pdf'
import ru from '../../images/Resume.RU.pdf'
import uz from '../../images/Resume Bobur.pdf'
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";

const Technologies = () => {

  var settings = {
    dots: true,
    arrows: true,
    infinite: true,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 1,
    initialSlide: 0,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true,
          dots: true,
          arrows: false
        }
      },
      {
        breakpoint: 718,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          arrows: false
        }
      }
    ]
  }
  return (
         <>
         <div className="containerr">
         <div className='label'>
          <h1>Here it is shown <br /> what technologies I know</h1>
      </div>
      <Slider {...settings}>
        <div>
        <img className='img' src={html} alt="" />
        </div>
        <div>
        <img className='img' src={css} alt="" />
        </div>
        <div>
        <img className='img' src={js} alt="" />
        </div>
        <div>
        <img className='img' src={react} alt="" />
        </div>
        <div>
        <img className='img' src={redux} alt="" />
        </div>
        <div>
          <img className='img' src={ts} alt="" />
        </div>
     </Slider>
      <div className="btns">
       <a href={en} download><button>Resume (EN)</button></a>
        <a href={ru} download><button>Resume (RU)</button></a>
        <a href={uz} download><button>Resume (UZ)</button></a>
      </div>
         </div>
         </>
  )
}

export default Technologies;