import React, { useContext, useState } from "react";
import "./Contact.scss";
import phone from "../../images/phone.svg";
import email from "../../images/email.svg";
import address from "../../images/address.svg";
import { useRef } from "react";
import emailjs from "@emailjs/browser";
import { ThemeContext } from "../../context";
import ButtonFixed from "../../ButtonFixed";

const Contact = () => {
  const formRef = useRef();
  const [done, setDone] = useState(false);

  const theme = useContext(ThemeContext);
  const darkMode = theme.state.darkMode;

  const handleSubmit = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_y7pnbtt",
        "template_8fex52j",
        formRef.current,
        "13Bwf0ppPAchqOPgZ"
      )
      .then(
        (result) => {
          console.log(result.text);
          setDone(true);
        },
        (error) => {
          console.log(error.text);
        }
      );
  };
  return (
    <div className="c">
      <div className="c-bg"></div>
      <div className="c-wrapper">
        <div className="c-left">
          <h1 className="c-title">Contact me...</h1>
          <div className="c-info">
            <div className="c-info-item">
              <img src={phone} alt="Phone" className="c-icon" />
              +998 99 855 73 85
            </div>
            <div className="c-info-item">
              <img src={email} alt="Email" className="c-icon" />
              boburhatamov7@gmail.com
            </div>
            <div className="c-info-item">
              <img src={address} alt="Address" className="c-icon" />
              Tashkent, Mirzo Ulugbek district Nishopuriy street number-21
            </div>
          </div>
        </div>
        <div className="c-right" id="contact">
          <p className="c-desc">
            <b>What's your story?</b> Get in touch. Always available for
            freelancing if the right project comes along me.
          </p>
          <form ref={formRef} onSubmit={handleSubmit}>
            <input
              style={{ backgroundColor: darkMode && "#333", color:darkMode && "white"}}
              type="text"
              placeholder="Name"
              name="user_name"
            />
            <input
              style={{ backgroundColor: darkMode && "#333",  color:darkMode && "white" }}
              type="text"
              placeholder="Subject"
              name="user_subject"
            />
            <input
              style={{ backgroundColor: darkMode && "#333" , color:darkMode && "white"}}
              type="text"
              placeholder="Email"
              name="user_email"
            />
            <textarea
              placeholder="Message"
              name="message"
              rows="5"
              style={{ backgroundColor: darkMode && "#333", color:darkMode && "white" }}
            ></textarea>
            <button>Submit</button>
            {done && window.location.reload()}
          </form>
        </div>
      </div>
      <div className="c-footer">
        <h3>It is a long established fact that a reader will be distracted by the readable content.</h3>
      </div>
      <ButtonFixed/>
    </div>
  );
};

export default Contact;
