import React from "react";
import "./About.scss";
import coder from "../../images/Coder.png";
import award from "../../images/award.png";
import { LazyLoadImage } from 'react-lazy-load-image-component';

const About = () => {
  return (
    <div className="a">
      <div className="a-left">
        <div className="a-card "></div>
        <div className="a-card">
          <img src={coder} alt="IMG" className="a-img" />
        </div>
      </div>
      <div className="a-right">
        <h1 className="a-title">About Me</h1>
        <p className="a-sub">
          <b>Assalamu Alaykum,</b> I was born in Tashkent in 2002 and I very
          flexible person.
        </p>
        <p className="a-desc">
          I graduated school in 2020. I was very interested in English,
          Russian and Arabic languages. And I spent a lot of time studying them.{" "}
          <br />
          - English Level - B1 <br />
          - Russian Level - B2 <br />
          - Arabic Level - A2 <br />
          And in 2021, my interest in IT increased. And I started learning
          Frontend. In 6 months, I completed the Frontend course at the{" "}
          <b>IT Academy</b> with an excellent "A" grade
        </p>
        <div className="a-award">
          <a
            href="http://localhost:3000/static/media/award.577608338b481e9a83cc.png"
            target="_blank" rel="noreferrer"
          >
            <LazyLoadImage src={award} alt="Award" className="a-award-img" />
          </a>
          {/*<h2 style={{fontWeight:"500"}}> Click here</h2>*/}
        </div>
      </div>
    </div>
  );
};

export default About;
