import React, {useContext} from "react";
import "./Intro.scss";
import bobur from '../../images/bobur.png'
import {LazyLoadImage} from 'react-lazy-load-image-component';
import {ThemeContext} from "../../context";

const Intro = () => {

    const theme = useContext(ThemeContext);
    const darkMode = theme.state.darkMode;

    return (
        <div className="i" id="intro">
            <div className="i-left">
                <div className="i-left-wrapper">
                    <h2 className="i-intro">Hello, My name is</h2>
                    <h1 className="i-name">Bobur Khatamov</h1>
                    <div className="i-title">
                        <div className="i-title-wrapper">
                            <div className="i-title-item">Web Developer</div>
                            <div className="i-title-item">Muslim</div>
                            <div className="i-title-item">Frontend Developer</div>
                            <div className="i-title-item">React.JS Developer</div>
                            <div className="i-title-item">JavaScript(TS) Lover</div>
                        </div>
                    </div>
                    <p className="i-desc">
                        I design and develop services for customers of all sizes, specializing in creating stylish,
                        modern websites, webservices and online stores.
                    </p>
                </div>
                <a className="i-scroll" href="#contact">
                    <svg style={{color: darkMode && "white"}} className="w-6 h-6" fill="none" stroke="currentColor"
                         viewBox="0 0 24 24"
                         xmlns="http://www.w3.org/2000/svg">
                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2"
                              d="M9.75 17L9 20l-1 1h8l-1-1-.75-3M3 13h18M5 17h14a2 2 0 002-2V5a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z"></path>
                    </svg>
                </a>
            </div>
            <div className="i-right">
                <div className="i-bg"></div>
                <LazyLoadImage src={bobur
                } alt="Bobur" className="i-img"/>
            </div>
        </div>
    );
};

export default Intro;
